export default {
  fetchOnServer: true,
  async fetch() {
    const pages = await this.$content("page").fetch();
    this.pages = pages;
  },
  data() {
    return {
      isOpen: false,
      links: null,
      pages: null,
    };
  },
  computed: {
    info() {
      return this.$store.state.info;
    },
    categories() {
      return this.$store.state.categories;
    },
  },
  methods: {
    open() {
      this.isOpen = true;
    },
    close() {
      this.isOpen = false;
    },
    toggle() {
      // Look for .hamburger
      this.$store.commit("toggleMenuState");

      var hamburger = document.querySelector(".hamburger");
      // On click

      // Toggle class "is-active"

      if (this.isOpen) {
        this.close();
      } else {
        this.open();
      }
    },
  },
};
